<template>
  <div class="hello">
    <b-modal
    id="productModal"
    size="xl"
    no-close-on-backdrop
    hide-header-close
    title=""
    @cencel="hideModal"
    @ok="updateProduct">
      <div>
        <b-row>
          <b-col cols="6">
            <div>
            <label for="pic"></label>
            <b-form-input id="pic" v-model="tempProduct.imageUrl[0]" placeholder="">
            </b-form-input>
            </div>
          </b-col>
          <b-col cols="6">
            <div>
            <label for="title"></label>
            <b-form-input id="title" type="text" v-model="tempProduct.title" placeholder="">
            </b-form-input>
            <label for="category"></label>
            <b-form-select v-model="tempProduct.category" :options="options">
            </b-form-select>
            <label for="origin_price"></label>
            <b-form-input
            id="origin_price" type="number" v-model="tempProduct.origin_price" placeholder="">
            </b-form-input>
            <label for="price"></label>
            <b-form-input id="price" type="number" v-model="tempProduct.price" placeholder="">
            </b-form-input>
            <label for="unit"></label>
            <b-form-input id="unit" type="text" v-model="tempProduct.unit" placeholder="">
            </b-form-input>
            <label for="content"></label>
            <b-form-textarea
            id="content" type="text" v-model="tempProduct.content" placeholder="">
            </b-form-textarea>
            <label for="description"></label>
            <b-form-textarea
            id="description" type="text" v-model="tempProduct.description" placeholder="">
            </b-form-textarea>
            </div>
          </b-col>
        </b-row>
      </div>
    </b-modal>
  </div>
</template>

<script>
export default {
  name: 'Product',
  data() {
    return {
      tempProduct: {
        imageUrl: [],
      },
      options: [
        { value: null, text: '' },
        { value: '', text: '' },
        { value: '', text: '' },
        { value: '', text: '' },
        { value: '', text: '' },
        { value: '', text: '' },
        { value: 'd', text: 'This one is disabled', disabled: true },
      ],
    };
  },
  props: {
    user: {
      type: Object,
      default() {
        return {};
      },
    },
    isNew: {
      type: Boolean,
      default() {
        return {};
      },
    },
  },
  methods: {
    getProduct(id) {
      const api = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_UUID}/admin/ec/product/${id}`;
      this.$http.get(api).then((res) => {
        this.$bvModal.show('productModal');
        this.tempProduct = res.data.data;
        console.log(this.tempProduct);
      }).catch((error) => {
        console.log(error);
      });
    },
    updateProduct() {
      let httpMethod = 'post';
      let api = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_UUID}/admin/ec/product/`;
      if (!this.isNew) {
        api = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_UUID}/admin/ec/product/${this.tempProduct.id}`;
        httpMethod = 'patch';
      }
      console.log(httpMethod);
      this.$http[httpMethod](api, this.tempProduct).then((res) => {
        console.log(res);
        this.tempProduct = { imageUrl: [] };
        this.$bvModal.hide('productModal');
        this.$emit('update');
      }).catch((error) => {
        console.log(error);
      });
    },
    hideModal() {
      this.$bvModal.hide('productModal');
      this.tempProduct = { imageUrl: [] };
      console.log('hideModal');
    },
  },
};
</script>

